@import url(https://fonts.googleapis.com/css?family=Product%20Sans:100,200,300,400,600,700,800,900);
/* @import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700,800,900'); */
/* @import url('https://fonts.googleapis.com/css?family=Roboto:400,600,700,800,900'); */

body {
  margin: 0px;
  font-family: 'Product Sans',Arial,Helvetica,sans-serif;
  /* font-family: 'Montserrat', sans-serif; */
  /* font-family: 'Roboto', sans-serif; */
  color: #fff;
  background: #333;
}

button{
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 4;
  width: 100px;
  height: 50px;
  border-radius: 5px
}

.logo{
  text-shadow: 0 0.025em 0 rgba(0,0,0,0.1);
  text-decoration: none;
  color: #fff;
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 22px;
  z-index: 5;
  letter-spacing: 0.5px;
  font-weight: 300;
}
.logoBold{
  font-weight: 400;
}
.squareContainer{
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 5;
}

.gridChooser{
  display: grid;
  grid: repeat(5,1fr) / repeat(5,1fr);
  width: 100%;
  height: 100%;
  
}
.square {
  margin: 1px;
  background: #fff;
  opacity: 0.7;
  cursor: pointer;
}
.square:hover{
  opacity: 1;
}
.tile {
    overflow: hidden;
    position: relative;
    width: 100%;
}
.card {
     display: grid;
     grid-template-columns: 1fr;
     -webkit-align-content: center;
             align-content: center; 
    height: 100%;
    margin: 0px;
    padding: 0px;
    position: absolute;
    text-transform: capitalize;
    width: 100%;
}
.typing {
    text-shadow: 0 0.025em 0 rgba(0,0,0,0.1);
    word-wrap: break-word;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
    margin: 10px;
    /* font-size: 2em; */
    font-size: calc(1.4em + 1vw);
    font-weight: 400;
  }
  #cursor {
    -webkit-animation: blink .8s steps(1) infinite;
            animation: blink .8s steps(1) infinite;
    border-left: 1px solid #fff;
  }
  
  @-webkit-keyframes blink {
    50% {
      border-color: transparent;
    }
  }
  
  @keyframes blink {
    50% {
      border-color: transparent;
    }
  }
  
  .fade-in {
    opacity: 1;
    -webkit-animation-name: fadeInOpacity;
            animation-name: fadeInOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
  }
  
  @-webkit-keyframes fadeInOpacity {
    0% {
      opacity: 0;
      }
      100% {
      opacity: 1;
      }
  }
  
  @keyframes fadeInOpacity {
    0% {
      opacity: 0;
      }
      100% {
      opacity: 1;
      }
  }
.left-exit {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.left-exit.left-exit-active {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all 600ms ease;
}

.left-exit-done{
    display: none;
}
.right-exit {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.right-exit.right-exit-active {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all 600ms ease;
}
.top-exit {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.top-exit.top-exit-active {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: all 600ms ease;
}

.bottom-exit {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.bottom-exit.bottom-exit-active {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: all 600ms ease;
}

